import {Component} from '@angular/core';
import {HelpersService} from '../helpers.service';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Storage} from "@ionic/storage";
import {Observable} from "rxjs";
import {ContentComponent} from "../content/content.component";
import {ModalController} from "@ionic/angular";

@Component({
    selector: 'app-tab2',
    templateUrl: 'tab2.page.html',
    styleUrls: ['tab2.page.scss']
})
export class Tab2Page {
    observableEvents: Observable<{}[]>;
    path: string;
    events = [];
    filteredEvents = [];
    searchBar: any;
    private itemsCollection: AngularFirestoreCollection<any>;

    constructor(
        private helper: HelpersService,
        private db: AngularFirestore,
        private storage: Storage,
        private modalCtrl: ModalController,
    ) {
        this.ionViewWillEnter();
    }

    eventIsActive(event: any): boolean {
        if (event.startTime === undefined || event.endTime === undefined) {
            return false;
        }
        const now = new Date().getTime() / 1000;
        const startTime = event.startTime.seconds;
        const endTime = event.endTime.seconds;
        return startTime <= now && now <= endTime;
    }

    inTheFuture(event: any) {
        return (new Date().getTime() / 1000 < event.startTime.seconds)
    }

    public scrollIntoView(event: any): boolean {
        console.log("Trying to scroll to event", event);
        const element = document.getElementById(event.startTime.seconds);
        if (element != null) {
            console.log("Element found, scrolling to", element);
            element.classList.add("active");
            element.scrollIntoView({behavior: "smooth"});
            return true;
        } else {
            console.warn("Element could not be found...", event);
            return false;
        }
    }

    ionViewWillEnter() {
        this.setupDBConnection()
            .then(() => {
                this.copyEventsFromPromiseIntoArray();
            });
    }

    showContentModal(data: any) {
        this.helper.showContentModal(data);
    }

    copyEventsFromPromiseIntoArray() {
        this.observableEvents.forEach(events => {
                this.events = [];
                for (let event of events) {
                    this.events.push(event);
                }
                this.filteredEvents = this.events;
            }
        );
    }

    scrollToCurrentlyActiveOrNextEvent() {
        this.filteredEvents = this.events;
        this.searchBar = '';
        for (let e of this.events) {
            if (this.eventIsActive(e) || this.inTheFuture(e)) {
                this.scrollIntoView(e);
                return;
            } else {
                this.removeActiveClass(e);
            }
        }
    }

    filterItems(ev: any) {
        const val = ev.target.value;
        if (val && val.trim() !== ' ') {
            this.filteredEvents = this.events.filter((item) => {
                return item.title.toLowerCase().includes(val.toLowerCase());
            });
        } else {
            this.filteredEvents = this.events;
        }
    }

    async createNewEvent() {
        const modal = await this.modalCtrl.create(
            {
                component: ContentComponent,
                componentProps: {
                    data: {newThing: true}
                }
            }
        );
        await modal.present();
        modal.onDidDismiss().then((e) => {
            if (e.data) {
                console.log("SEND THIS TO THE SERVER!", e.data);
                this.addItem(e.data);
            }
        });

    }

    private setupDBConnection() {
        return this.storage.get('domainPath').then(path => {
            if (this.path !== path) {
                this.itemsCollection = this.db.collection(path + 'program', ref => ref.orderBy('startTime'));
                this.observableEvents = this.itemsCollection.valueChanges();
                this.observableEvents.subscribe(() => {
                    this.copyEventsFromPromiseIntoArray();
                });
                this.path = path;
            }
        });
    }

    private removeActiveClass(e: any) {
        const element = document.getElementById(e.startTime.seconds);
        if (element != null) {
            element.classList.remove("active");
        }
    }

    private addItem(item: any) {
        this.itemsCollection.add(item);
    }

    private removeItem(item: any) {
        this.itemsCollection.doc(item).delete();
    }
}
